import usersData from '../data/users.json';

class AuthService {
  constructor() {
    //          
    const users = JSON.parse(localStorage.getItem('users') || '[]');
    if (users.length === 0) {
      //      
      localStorage.setItem('users', JSON.stringify(usersData.users));
    }
  }

  login(employeeId, password) {
    const users = JSON.parse(localStorage.getItem('users') || '[]');
    
    //     bcrypt    
    //       
    const user = users.find(
      (u) => u.employeeId === employeeId && u.password === password
    );

    if (user) {
      //       
      const { password, ...userWithoutPassword } = user;
      localStorage.setItem('currentUser', JSON.stringify(userWithoutPassword));
      return userWithoutPassword;
    }

    throw new Error('      ');
  }

  logout() {
    localStorage.removeItem('currentUser');
  }

  getCurrentUser() {
    const user = localStorage.getItem('currentUser');
    return user ? JSON.parse(user) : null;
  }

  isAuthenticated() {
    return !!this.getCurrentUser();
  }

  isAdmin() {
    const user = this.getCurrentUser();
    return user && user.role === 'admin';
  }
  
  //   
  addUser(userData) {
    const users = JSON.parse(localStorage.getItem('users') || '[]');
    
    //      
    if (users.some(user => user.employeeId === userData.employeeId)) {
      throw new Error('   ');
    }
    
    //   
    const newUser = {
      id: users.length > 0 ? Math.max(...users.map(u => u.id)) + 1 : 1,
      employeeId: userData.employeeId,
      name: userData.name,
      password: userData.password, //       
      role: userData.role || 'user',
      createdAt: new Date().toISOString()
    };
    
    //   
    const updatedUsers = [...users, newUser];
    localStorage.setItem('users', JSON.stringify(updatedUsers));
    
    //     
    const { password, ...userWithoutPassword } = newUser;
    return userWithoutPassword;
  }
  
  //  
  deleteUser(userId) {
    const users = JSON.parse(localStorage.getItem('users') || '[]');
    
    //    
    const adminUsers = users.filter(user => user.role === 'admin');
    
    //          
    if (adminUsers.length === 1 && adminUsers[0].id === userId) {
      throw new Error('     ');
    }
    
    //  
    const updatedUsers = users.filter(user => user.id !== userId);
    localStorage.setItem('users', JSON.stringify(updatedUsers));
    
    return true;
  }
  
  //    
  getUsers() {
    const users = JSON.parse(localStorage.getItem('users') || '[]');
    //     
    return users.map(({ password, ...userWithoutPassword }) => userWithoutPassword);
  }
}

export const authService = new AuthService();