import React, { createContext, useContext } from 'react';

//    
const SidebarContext = createContext();

//    
export const SidebarProvider = ({ children }) => {
  //        
  const menuItems = [
    { id: 'bloodbank', label: ' ', icon: '', path: '/bloodbank', showForRegularUser: true },
    { id: 'emergency', label: ' ', icon: '', path: '/emergency', showForRegularUser: true },
    { id: 'lab', label: ' ', icon: '', path: '/lab', showForRegularUser: true },
    { id: 'rad', label: ' ', icon: '', path: '/rad', showForRegularUser: true },
    { id: 'operations', label: ' ', icon: '', path: '/operations', showForRegularUser: true },
    { id: 'admin', label: ' ', icon: '', path: '/admin' },
  ];

  return (
    <SidebarContext.Provider value={{ menuItems }}>
      {children}
    </SidebarContext.Provider>
  );
};

//      
export const useSidebar = () => {
  const context = useContext(SidebarContext);
  if (!context) {
    throw new Error('useSidebar    SidebarProvider');
  }
  return context;
};