import React, { useEffect, useRef, useState } from 'react';
import { Line } from 'react-chartjs-2';
import { 
  Chart as ChartJS, 
  CategoryScale, 
  LinearScale, 
  PointElement, 
  LineElement, 
  Title, 
  Tooltip, 
  Legend,
  Filler
} from 'chart.js';

//  Chart.js -      
ChartJS.register(
  CategoryScale, 
  LinearScale, 
  PointElement, 
  LineElement, 
  Title, 
  Tooltip, 
  Legend,
  Filler
);

/**
 *           
 * @param {Object} props  
 * @param {Array|Array[]} props.data         
 * @param {Array} props.labels  ( )
 * @param {String} props.title   
 * @param {String|Array} props.label       
 * @param {String|Array} props.backgroundColor         
 * @param {String|Array} props.borderColor      
 * @param {Number} props.height    
 * @param {Number} props.yAxisMin    Y
 * @param {Number} props.yAxisMax    Y
 * @param {Boolean} props.isPercentage    
 * @param {Boolean} props.isTime    ()
 * @param {Number} props.benchmark    
 * @param {String} props.yAxisLabel   Y
 * @param {String} props.direction   (rtl/ltr)
 * @param {Boolean} props.multipleDatasets    
 */
const TimeComparisonChart = ({ 
  data = [], 
  labels = [], 
  title, 
  label,
  backgroundColor = 'rgba(54, 162, 235, 0.2)',
  borderColor = 'rgba(54, 162, 235, 1)',
  height = 300,
  yAxisMin,
  yAxisMax,
  isPercentage = false,
  isTime = false,
  benchmark = null,
  yAxisLabel,
  direction = 'ltr',
  multipleDatasets = false
}) => {
  const chartRef = useRef(null);
  const [isVisible, setIsVisible] = useState(false);
  const [renderedOnce, setRenderedOnce] = useState(false);
  const observerRef = useRef(null);
  const containerRef = useRef(null);
  
  //      (    )
  const formatValue = (value) => {
    if (isTime && typeof value === 'number') {
      //        
      const hours = Math.floor(value / 60);
      const minutes = Math.floor(value % 60);
      return `${hours}:${minutes.toString().padStart(2, '0')}`;
    } else if (isPercentage && typeof value === 'number') {
      //      
      return `${value.toFixed(2)}%`;
    }
    return value;
  };

  //      
  const formatYAxisLabel = (value) => {
    if (isPercentage) {
      //      
      return `${value.toFixed(2)}%`;
    }
    return value;
  };

  //   
  const options = {
    responsive: true,
    maintainAspectRatio: false,
    animation: {
      duration: isVisible ? 1000 : 0 //         
    },
    plugins: {
      legend: {
        position: 'top',
        align: 'end',
        rtl: direction === 'rtl',
        labels: {
          boxWidth: 12,
          font: {
            family: 'Tajawal, Arial, sans-serif',
            size: 12
          },
          color: '#333333'
        }
      },
      tooltip: {
        backgroundColor: 'rgba(0, 0, 0, 0.8)',
        titleFont: {
          family: 'Tajawal, Arial, sans-serif',
          size: 14
        },
        bodyFont: {
          family: 'Tajawal, Arial, sans-serif',
          size: 13
        },
        padding: 10,
        callbacks: {
          label: function(context) {
            let label = context.dataset.label || '';
            if (label) {
              label += ': ';
            }
            if (context.parsed.y !== null) {
              label += formatValue(context.parsed.y);
            }
            return label;
          }
        }
      },
      title: {
        display: !!title,
        text: title || '',
        font: {
          family: 'Tajawal, Arial, sans-serif',
          size: 16,
          weight: 'bold'
        },
        color: '#333333',
        padding: {
          top: 10,
          bottom: 20
        }
      }
    },
    scales: {
      x: {
        reverse: direction === 'rtl',
        grid: {
          display: false
        },
        ticks: {
          font: {
            family: 'Tajawal, Arial, sans-serif',
            size: 11
          },
          maxRotation: 30,
          minRotation: 0
        }
      },
      y: {
        min: yAxisMin,
        max: yAxisMax,
        grid: {
          color: 'rgba(0, 0, 0, 0.05)'
        },
        ticks: {
          font: {
            family: 'Tajawal, Arial, sans-serif',
            size: 11
          },
          callback: function(value) {
            return formatYAxisLabel(value);
          }
        },
        title: {
          display: !!yAxisLabel,
          text: yAxisLabel || '',
          font: {
            family: 'Tajawal, Arial, sans-serif',
            size: 12
          }
        }
      }
    },
    //    
    elements: {
      line: {
        tension: 0.3 //   
      },
      point: {
        //     
        radius: 3,
        hoverRadius: 5,
        hitRadius: 8 
      }
    },
    //       
    devicePixelRatio: isVisible ? window.devicePixelRatio : 1
  };

  //   
  let chartData = {
    labels,
    datasets: []
  };

  //      
  if (multipleDatasets && Array.isArray(data) && data.length > 0 && Array.isArray(data[0])) {
    //           data    
    const labels_array = Array.isArray(label) ? label : [label, label, label];
    const bgColors_array = Array.isArray(backgroundColor) ? backgroundColor : [backgroundColor, backgroundColor, backgroundColor];
    const borderColors_array = Array.isArray(borderColor) ? borderColor : [borderColor, borderColor, borderColor];
    
    //      
    data.forEach((dataSet, index) => {
      chartData.datasets.push({
        label: labels_array[index] || ` ${index + 1}`,
        data: dataSet,
        fill: false, //    
        backgroundColor: bgColors_array[index] || `rgba(${Math.floor(Math.random() * 255)}, ${Math.floor(Math.random() * 255)}, ${Math.floor(Math.random() * 255)}, 0.2)`,
        borderColor: borderColors_array[index] || `rgba(${Math.floor(Math.random() * 255)}, ${Math.floor(Math.random() * 255)}, ${Math.floor(Math.random() * 255)}, 1)`,
        borderWidth: 2,
        pointBackgroundColor: borderColors_array[index] || `rgba(${Math.floor(Math.random() * 255)}, ${Math.floor(Math.random() * 255)}, ${Math.floor(Math.random() * 255)}, 1)`,
        tension: 0.3
      });
    });
  } else {
    //        
    chartData.datasets.push({
      label: Array.isArray(label) ? label[0] : label || '',
      data: data,
      fill: true,
      backgroundColor: Array.isArray(backgroundColor) ? backgroundColor[0] : backgroundColor,
      borderColor: Array.isArray(borderColor) ? borderColor[0] : borderColor,
      borderWidth: 2,
      pointBackgroundColor: Array.isArray(borderColor) ? borderColor[0] : borderColor,
      tension: 0.3
    });
  }

  //   
  if (benchmark !== null) {
    chartData.datasets.push({
      label: ' ',
      data: Array(labels.length).fill(benchmark),
      fill: false,
      backgroundColor: 'transparent',
      borderColor: 'rgba(255, 99, 132, 1)',
      borderWidth: 2,
      borderDash: [5, 5],
      pointRadius: 0
    });
  }

  //  Intersection Observer   (Lazy Loading)
  useEffect(() => {
    //    
    if (typeof window === 'undefined' || !window.IntersectionObserver) {
      setIsVisible(true);
      return () => {};
    }

    //   
    observerRef.current = new IntersectionObserver((entries) => {
      const [entry] = entries;
      setIsVisible(entry.isIntersecting);
      
      //      
      if (entry.isIntersecting && !renderedOnce) {
        setRenderedOnce(true);
      }
    }, {
      root: null,
      rootMargin: '0px',
      threshold: 0.1
    });

    //  
    if (containerRef.current) {
      observerRef.current.observe(containerRef.current);
    }

    return () => {
      if (observerRef.current && containerRef.current) {
        observerRef.current.disconnect();
      }
    };
  }, [renderedOnce]);

  return (
    <div ref={containerRef} style={{ height: `${height}px`, direction: direction }}>
      {(isVisible || renderedOnce) ? (
        <Line 
          ref={chartRef}
          data={chartData} 
          options={options} 
        />
      ) : (
        <div className="flex items-center justify-center h-full bg-gray-50">
          <div className="text-gray-400">   ...</div>
        </div>
      )}
    </div>
  );
};

export default React.memo(TimeComparisonChart);